const getBarcode = (array) => {
  const array_base_one = array.measure_units_properties.find(item => item[0] === ". .");
  const index = array_base_one[1].findIndex(value => value === `${array.multiplicity} `);
  return array.measure_units_properties.find(item => item[0] === " . .")[1][index];
};

export default getBarcode;